.box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
}

.arrow {
  background: white;
  border-radius: 3px;
  border: 2px solid #ccc;
  width: 30px;
  height: 30px;
  margin-right: 20px;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.label {
  font-weight: 600;
  color: #ccc;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.checked .arrow {
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  background: rgb(76, 217, 100);
  border: 2px solid rgb(76, 217, 100);
}

.checked .label {
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  color: black;
}



