
.buttonFetch {
  cursor: pointer;
  text-transform: capitalize;
  font-size: 17px;
  outline: none;
  border: none;
  width: 150px;
  height: 50px;
  color: white;
  background: rgb(76, 217, 100);
  border-radius: 3px;
  -webkit-box-shadow: 0 5px 30px 0 rgba(76, 217, 100, .4);
          box-shadow: 0 5px 30px 0 rgba(76, 217, 100, .4);
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.buttonFetch:hover {
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  -webkit-box-shadow: 0 0 10px 0 rgba(76, 217, 100, .4);
          box-shadow: 0 0 10px 0 rgba(76, 217, 100, .4);
}



